.popup-images {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  justify-content: center;
  align-items: center;
  transition: visibility 0.5s, opacity 0.5s;
}

.popup-images::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  opacity: 0.9;
}
