.profile__title {
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.1;
  max-width: 294px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media screen and (max-width: 500px) {
  .profile__title {
    font-size: 27px;
    line-height: 1.2;
  }
}
